'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { toast } from 'sonner';

import { Button } from '@/components/ui/button';
import {
    AlertDialog,
    AlertDialogAction,
    AlertDialogCancel,
    AlertDialogContent,
    AlertDialogDescription,
    AlertDialogFooter,
    AlertDialogHeader,
    AlertDialogTitle,
    AlertDialogTrigger,
} from '@/components/ui/alert-dialog';

type DailyExceptionDeleteButtonProps = {
    professionalId: string;
    dateISO: string;

    /**
     * Se quiser controlar a remo√ß√£o por fora (ex: chamar API),
     * retorne uma Promise e o bot√£o fica em loading at√© concluir.
     *
     * Observa√ß√£o:
     * - Se voc√™ passar onDelete, este componente N√ÉO dispara toast/refresh
     *   para evitar duplica√ß√£o (o pai pode controlar isso).
     * - Se N√ÉO passar, ele chama a API, mostra toast e d√° refresh.
     */
    onDelete?: (args: {
        professionalId: string;
        dateISO: string;
    }) => void | Promise<void>;
};

export function DailyExceptionDeleteButton({
    professionalId,
    dateISO,
    onDelete,
}: DailyExceptionDeleteButtonProps) {
    const router = useRouter();
    const [isPending, setIsPending] = useState(false);

    const handleDelete = async () => {
        try {
            setIsPending(true);

            // ‚úÖ Se o pai controla, s√≥ delega (sem duplicar toast/refresh)
            if (onDelete) {
                await onDelete({ professionalId, dateISO });
                return;
            }

            // ‚úÖ Caso contr√°rio, este componente resolve tudo
            const res = await fetch(
                `/api/professional/availability/exceptions?dateISO=${encodeURIComponent(
                    dateISO
                )}`,
                {
                    method: 'DELETE',
                    headers: { 'Content-Type': 'application/json' },
                }
            );

            const json = (await res.json()) as { ok: boolean; error?: string };

            if (!res.ok || !json?.ok) {
                toast.error(json?.error ?? 'Erro ao remover exce√ß√£o.');
                return;
            }

            toast.success('Exce√ß√£o removida com sucesso.');
            router.refresh();
        } catch {
            toast.error('Falha ao remover exce√ß√£o. Tente novamente.');
        } finally {
            setIsPending(false);
        }
    };

    return (
        <AlertDialog>
            <AlertDialogTrigger asChild>
                <Button
                    type="button"
                    variant="destructive"
                    size="sm"
                    disabled={isPending}
                >
                    Excluir
                </Button>
            </AlertDialogTrigger>

            <AlertDialogContent>
                <AlertDialogHeader>
                    <AlertDialogTitle>
                        Remover exce√ß√£o deste dia?
                    </AlertDialogTitle>
                    <AlertDialogDescription>
                        Este dia voltar√° a seguir apenas o padr√£o semanal de
                        disponibilidade. Os hor√°rios customizados ser√£o
                        apagados.
                    </AlertDialogDescription>
                </AlertDialogHeader>

                <AlertDialogFooter>
                    <AlertDialogCancel disabled={isPending}>
                        Cancelar
                    </AlertDialogCancel>

                    <AlertDialogAction asChild>
                        <Button
                            onClick={handleDelete}
                            disabled={isPending}
                            variant="destructive"
                            type="button"
                        >
                            {isPending ? 'Removendo...' : 'Remover exce√ß√£o'}
                        </Button>
                    </AlertDialogAction>
                </AlertDialogFooter>
            </AlertDialogContent>
        </AlertDialog>
    );
}
